ul{
	list-style: none;
}

.event-list, .guest-list{
	li{
		display:block;
		vertical-align:middle;
		padding: 7px 0px;
		list-style: none;
	}
}

.btn{
	padding: 3px 10px;
	margin: 0px 3px;
}

[data-action="selectDay"]{
	cursor:pointer;
}

[data-action="next"],
[data-action="previous"]{
	display:block;
	cursor:pointer;
}

.bootstrap-datetimepicker-widget{
	font-size: 12px !important;
}